import React, { useState } from "react";
import { Link } from "react-router-dom";

const priceList = [
  {
    title: "Free",
    price: "0",
    priceUnit: "",
    priceBefore: "50,000",
    list: [" ", "  ", "   "],
    etc: "",
    link: "/pricing",
  },
  {
    title: "Basic",
    price: "250,000",
    priceUnit: "",
    priceBefore: "300,000",
    list: ["Free +", "   1", " 1"],
    etc: "",
    link: "/pricing",
  },
  {
    title: "Standard",
    price: "350,000",
    priceUnit: "",
    priceBefore: "550,000",
    list: ["Basic +", "   ", " IP   "],
    etc: "",
    link: "/pricing",
  },
  {
    title: "Standard Pro",
    price: "175",
    priceUnit: "",
    priceBefore: "2,200,000",
    list: ["Standard +", " 2", " 1", ""],
    etc: "   -   <br/> 1 ,  ",
    link: "/pricing",
  },
  {
    title: "Premium",
    price: "300",
    priceUnit: "",
    priceBefore: "3,750,000",
    list: ["Standard Pro +", " 5", " 1", "  (   6  )"],
    etc: "   -   <br/> 1 ,  ",
    link: "/pricing",
  },
];

const Pricing = (props) => {
  const [activeGrid, setActiveGrid] = useState(null);

  return (
    <section className="wpo-pricing-section section-padding">
      <div className="container">
        <div className="row align-items-center justify-content-center">
          <div className="col-lg-5">
            <div className="wpo-section-title">
              <h2>   </h2>
            </div>
          </div>
        </div>
        <div className="pricing-grids clearfix">
          <p className="pricing-etc">(VAT)</p>
          {priceList.map((value, index) => (
            <div
              key={index}
              className={`grid ${activeGrid === index ? "active" : ""}`}
              onMouseEnter={() => setActiveGrid(index)}
              onMouseLeave={() => setActiveGrid(null)}
            >
              <div className="type">
                <h5>{value.title}</h5>
              </div>
              <div className="pricing-header">
                <div>
                  <h3 className="price">
                    {value.price}
                    <span>{value.priceUnit}</span>
                  </h3>
                  <p>{value.priceBefore}</p>
                </div>
              </div>
              <div className="pricing-body">
                <ul>
                  {value.list.map((item, index) => (
                    <li key={index}>{item}</li>
                  ))}
                </ul>
                {value.etc && <p dangerouslySetInnerHTML={{ __html: value.etc }}></p>}
                <Link to={value.link} className="get-started">
                  Get Started
                </Link>
              </div>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
};

export default Pricing;
